.btn-simple {
  --btn-simple-padding: 21px 48px;
  --btn-simple-text-size: 14px;
  --btn-simple-mt: 10px;
  --btn-simple-border-radius: 0px;
  --btn-simple-transition-in: 0.2s;
  --btn-simple-transition-out: 0.2s;
  --btn-simple-padding-xs: 4px 5px;
  --btn-simple-text-size-xs: 10px;
  --btn-simple-padding-sm: 6px 10px;
  --btn-simple-text-size-sm: 12px;
  --btn-simple-padding-lg: 10px 18px;
  --btn-simple-text-size-lg: 18px;
  --btn-group-separation: 18px;
}
//@include generate-btn-simple(var(--name, $color, $bg, $color-hover, $bg-hover, $bg-active, $color-disabled, $bg-disabled));

@include generate-btn-simple(1, white, var(--color-highlight), white, #444, #444, white, var(--color-disabled));
@include generate-btn-simple(2, white, var(--color-danger), white, #444, #444, white, var(--color-disabled));

@include generate-btn-simple(3, white, var(--color-info), white, #444, #444, white, var(--color-disabled));

html {
  scroll-behavior: smooth;
}

.btn-simple {
  font-family: Avenir;
  font-size: 18px;
  border-radius: 50px;
}

.big-blob {
  position: absolute;
  top: 0;
  right: 0;
  width: 100vw;
  background: red;
  clip-path: url(#svgClipPathID);
  @media (max-width: $screen-xs-max) {
    display: none;
  }
}

#big-blob-svg,
#small-blobs-svg {
  position: absolute;
}

.svg {
  position: absolute;
  width: 0;
  height: 0;
}
.hero {
  min-height: 60vh;
}
.big-blob {
  width: 70vw;
  height: 80%;
  background: linear-gradient(to bottom, var(--color-highlight), var(--color-bg-2));
  background-size: cover;
  clip-path: url(#big-blob);
  position: absolute;
  top: 0;
  right: 0;
  animation: move 10s ease-in-out infinite;
  translate: 20% -20%;
  z-index: -1;
  pointer-events: none;
  @media (max-width: $screen-xs-max) {
    width: 150vw;
  }
}

@keyframes move {
  0% {
    transform: scale(1) translate(100px 100px);
  }
  38% {
    transform: scale(1, 1.2) rotate(30deg);
  }
  40% {
    transform: scale(1, 1.2) rotate(30deg);
  }
  78% {
    transform: scale(1.2) rotate(-18deg);
  }
  80% {
    transform: scale(1.2) rotate(-15deg);
  }
  100% {
    transform: scale(1);
  }
}

.is-highlight {
  color: var(--color-highlight);
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
  @media (max-width: $screen-xs-max) {
    -webkit-text-stroke-width: 0;
  }
}

.header {
  position: static;
  top: 0;
  width: 100%;
  z-index: 9;
  padding: 12px 0;
  margin-bottom: 50px;
  // background: rgba(255, 255, 255, 0.1);
  // backdrop-filter: blur(7px);
  &__logo {
    width: 250px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__links {
    flex-basis: 20%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__link {
    color: #fff;
    font-weight: bold;
  }
}

.hero {
  &__item {
    translate: 10% 0%;
    display: flex;
    height: 444px;
    position: relative;
    button {
      cursor: pointer;
    }
    svg {
      transition: 0.5s;
      height: 100px;
      width: 100px;
      fill: white;
      transform: rotate(180deg);
      stroke: var(--color-bg-2);
    }
  }
  .iframe-container {
    position: absolute;
    top: 60px;
    left: 338px;
  }
}

body {
  overflow-x: hidden;
  font-family: "Avenir";
}

.img,
.picture {
  width: 100%;
  max-width: 100%;
}

.container--center {
  text-align: center;
}

.rfm-marquee {
  img {
    height: 80px;
    margin-right: 50px;
  }
}

