.iframe-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-top: 56.25%; /* 16:9 Aspect Ratio (divide 9 by 16 = 0.5625) */
  margin-bottom: 20px;
}

/* Then style the iframe to fit in the container div with full height and width */
.iframe-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.showcase {
  .showcase__video {
    position: absolute;
    // width: 43vw;
    // height: 24vw;
    left: 0;
    right: 0;
    margin: auto;
    bottom: 0;
    top: 0;
    @media (min-width: $screen-xs) {
      width: 40%;
      height: 50%;
    }
    @media (min-width: $screen-sm) {
      width: 340px;
      height: 190px;
    }
    @media (min-width: $screen-md) {
      width: 460px;
      height: 260px;
    }
    @media (min-width: $screen-lg) {
      width: 550px;
      height: 300px;
    }
    @media (min-width: $screen-xl) {
      width: 600px;
      height: 340px;
    }
  }
}

@function randomNum($min, $max) {
  $rand: random();
  $randomNum: $min + floor($rand * (($max - $min) + 1));

  @return $randomNum;
}
.blobs {
  min-height: 600px;
  position: relative;
}
.top-bloby,
.bottom-bloby {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 150px;
  background: var(--color-highlight);
  border-radius: 0 0 100% 100%;
  animation: height #{randomNum(10, 20)}s ease-in-out infinite;
}
.bottom-bloby {
  top: auto;
  bottom: 0;
  border-radius: 100% 100% 0 0;
}
.bloby {
  position: absolute;
  top: 0;
  left: 0;
  fill: var(--color-highlight);
  width: #{randomNum(5, 10)}vw;
  z-index: -1;
  animation: movy 20s ease-in-out infinite;
  transform-origin: 50% 50%;
  transform: translate(5vw, -12vh);
  &:nth-child(1) {
    left: 5%;
    width: #{randomNum(10, 24)}vw;
    animation: movy #{randomNum(10, 20)}s ease-in-out infinite;
  }
  &:nth-child(2) {
    left: 25%;
    width: #{randomNum(4, 24)}vw;
    animation: movy2 #{randomNum(10, 20)}s ease-in-out infinite;
  }
  &:nth-child(3) {
    left: 35%;
    width: #{randomNum(5, 24)}vw;
    animation: movy3 #{randomNum(10, 20)}s ease-in-out infinite;
  }
  &:nth-child(4) {
    left: 45%;
    width: #{randomNum(5, 24)}vw;
    animation: movy2 #{randomNum(10, 20)}s ease-in-out infinite;
  }
  &:nth-child(5) {
    left: 55%;
    width: #{randomNum(5, 24)}vw;
    animation: movy #{randomNum(10, 20)}s ease-in-out infinite;
  }
  &:nth-child(6) {
    left: 65%;
    width: #{randomNum(6, 24)}vw;
    animation: movy3 #{randomNum(10, 20)}s ease-in-out infinite;
  }
  &:nth-child(7) {
    left: 75%;
    width: #{randomNum(2, 24)}vw;
    animation: movy2 #{randomNum(10, 20)}s ease-in-out infinite;
  }
  &:nth-child(8) {
    left: 85%;
    width: #{randomNum(1, 24)}vw;
    animation: movy #{randomNum(10, 20)}s ease-in-out infinite;
  }
  &:nth-child(9) {
    left: 95%;
    width: #{randomNum(2, 24)}vw;
    animation: movy3 #{randomNum(10, 20)}s ease-in-out infinite;
  }
}
$top: -10vw;
$bottom: 30vw;

@keyframes height {
  0% {
    height: 10%;
  }

  58% {
    height: 15px;
  }

  100% {
    height: 10%;
  }
}

@keyframes movy {
  0% {
    transform: scale(1) translate(0, $bottom) rotate(12deg);
  }
  38% {
    transform: scale(1.4) translate(0, $top) rotate(#{randomNum(1, 45)}deg);
  }
  58% {
    transform: scale(1) translate(0, $top) rotate(#{randomNum(1, 45)}deg);
  }
  80% {
    transform: scale(1.4) translate(0, $top) rotate(#{randomNum(1, 45)}deg);
  }
  100% {
    transform: scale(1) translate(0, $bottom) rotate(12deg);
  }
}

@keyframes movy2 {
  0% {
    transform: scale(1) translate(0, $bottom) rotate(12deg);
  }
  38% {
    transform: scale(1.4) translate(0, $top) rotate(#{randomNum(1, 45)}deg);
  }
  58% {
    transform: scale(1) translate(0, $top) rotate(#{randomNum(1, 45)}deg);
  }
  80% {
    transform: scale(1.4) translate(0, $top) rotate(#{randomNum(1, 45)}deg);
  }
  100% {
    transform: scale(1) translate(0, $bottom) rotate(12deg);
  }
}
@keyframes movy3 {
  0% {
    transform: scale(1) translate(0, $bottom) rotate(12deg);
  }
  38% {
    transform: scale(1.4) translate(0, $top) rotate(#{randomNum(1, 45)}deg);
  }
  58% {
    transform: scale(1) translate(0, $top) rotate(#{randomNum(1, 45)}deg);
  }
  80% {
    transform: scale(1.4) translate(0, $top) rotate(#{randomNum(1, 45)}deg);
  }
  100% {
    transform: scale(1) translate(0, $bottom) rotate(12deg);
  }
}

.download-links {
  display: flex;
  justify-content: space-around;
  margin-bottom: 70px;
  @media (max-width: $screen-xs-max) {
    display: block;
  }
  a {
    margin-bottom: 12px;
  }
  img {
    width: 100px;
  }
  li {
    display: flex;
    flex-direction: column-reverse;
    text-align: center;
    @media (max-width: $screen-xs-max) {
      margin-bottom: 64px;
    }
  }
}

svg.logo .path {
  stroke-dasharray: 1320;
  stroke-dashoffset: 0;
  fill: #fff;
  animation: dash 60s linear alternate infinite;
}
@keyframes dash {
  0% {
    stroke-dashoffset: 1320;
    fill: #fff;
  }
  3% {
    stroke-dashoffset: 0;
    stroke-dasharray: 1320;
    fill: #fff;
  }
  5% {
    stroke-dashoffset: 0;
    stroke-dasharray: 1320;

    fill: inherit;
  }
  10% {
    stroke-dashoffset: 0;
    stroke-dasharray: 1320;

    fill: inherit;
  }
  100% {
    stroke-dashoffset: 0;
    stroke-dasharray: 1320;

    fill: inherit;
  }
}

.text-line {
  stroke-dasharray: 500;
  stroke-dashoffset: 500;
  animation: dashfont 5s linear forwards, filling 5s ease-in forwards;
  font-size: 80px;
}

@keyframes dashfont {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes filling {
  0%,
  90% {
    fill: #bac736;
    fill-opacity: 0;
  }
  100% {
    fill: #bac736;
    fill-opacity: 1;
  }
}
